
@font-face {
  font-family: 'Open Sans Regular';
  font-style: normal;
  font-weight: normal;
  src: url('./../fonts//Open_Sans/opensans-regular-webfont.woff') format('woff');
}


@font-face {
  font-family: 'Raleway Bold';
  font-style: normal;
  font-weight: bold;
  src: local('Raleway Bold'),
  url('./../fonts/Raleway/raleway-bold-webfont.woff2') format('woff2');
}
@font-face {
  font-family: 'Material Icons';
  font-style: normal;
  font-weight: 400;
  src: local('Material Icons'),
  url('./../fonts/MaterialIcons/Material Design.woff2') format('woff2');
}

.material-icons {
  font-family: 'Material Icons';
  font-weight: normal;
  font-style: normal;
  font-size: 24px;
  line-height: 1;
  letter-spacing: normal;
  text-transform: none;
  display: inline-block;
  white-space: nowrap;
  word-wrap: normal;
  direction: ltr;
  -webkit-font-feature-settings: 'liga';
  -webkit-font-smoothing: antialiased;
}




